<template>
  <div>
    <div id="background-container"></div>

    <div id="content-container">
      <h1>BACK</h1>

      <!-- Input field and SEND button -->
      <div>
        <input type="text" v-model="searchTerm" placeholder="Enter search term" />
        <button @click="fetchProducts">SEND</button>
      </div>

      <!-- Results Table -->
      <table v-if="product.name">
        <thead>
          <tr>
            <th>Name</th>
            <th>Type</th>
            <th>Unit</th>
            <th>Description</th>
            <th>Price</th>
            <th>In Stock</th>
            <th>Tags</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{ product.name }}</td>
            <td>{{ product.type }}</td>
            <td>{{ product.unit }}</td>
            <td>{{ product.description }}</td>
            <td>{{ product.price }}</td>
            <td>{{ product.inStock ? "Yes" : "No" }}</td>
            <td>{{ product.tags }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      searchTerm: "", // Binds to the input field
      product: {
        name: "",
        type: "",
        unit: "",
        description: "",
        price: "",
        inStock: false,
        tags: "",
      },
    };
  },
  methods: {
    async fetchProducts() {
      try {
        console.log("Backend URL:", process.env.VUE_APP_BACKEND_URL);
        console.log(`${process.env.VUE_APP_BACKEND_URL}/get-products-general?searchTerm=${this.searchTerm}`);

        const response = await axios.get(`${process.env.VUE_APP_BACKEND_URL}/get-products-general`, {
          params: { searchTerm: this.searchTerm },
        });


        if (response.data) {
          this.product = response.data; // Update the product with API response
        }
      } catch (error) {
        console.error("Error fetching products:", error);
        alert("Failed to fetch products. Please try again.");
      }
    },
  },
};
</script>

<style scoped>
#background-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-image: url('@/assets/lett1.webp') !important;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center center !important;
  background-attachment: fixed !important;
  margin: 0 !important;
  overflow: hidden !important;
}

#content-container {
  position: relative;
  z-index: 2;
  padding: 20px;
  background: rgba(255, 255, 255, 0.8);
  /* Add background to make text readable */
}

input {
  margin-right: 10px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 5px 10px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

table {
  margin-top: 20px;
  border-collapse: collapse;
  width: 100%;
}

table,
th,
td {
  border: 1px solid #ddd;
}

th,
td {
  padding: 8px;
  text-align: left;
}
</style>
